.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite; }

-webkit-keyframes pulse 0% {
  -webkit-transform: scale(1); }

-webkit-keyframes pulse 50% {
  -webkit-transform: scale(1.1); }

-webkit-keyframes pulse 100% {
  -webkit-transform: scale(1); }

@keyframes pulse {
  0% {
    transform: scale(1); }
  50% {
    transform: scale(1.1); }
  100% {
    transform: scale(1); } }

.pulse {
  -webkit-animation-name: pulse;
  animation-name: pulse; }

-webkit-keyframes rotateIn 0% {
  -webkit-transform-origin: center center;
  -webkit-transform: rotate(-200deg);
  opacity: 0; }

-webkit-keyframes rotateIn 100% {
  -webkit-transform-origin: center center;
  -webkit-transform: rotate(0);
  opacity: 1; }

@keyframes rotateIn {
  0% {
    transform-origin: center center;
    transform: rotate(-200deg);
    opacity: 0; }
  100% {
    transform-origin: center center;
    transform: rotate(0);
    opacity: 1; } }

.rotateIn {
  -webkit-animation-name: rotateIn;
  animation-name: rotateIn; }

-webkit-keyframes bounce 0%, -webkit-keyframes bounce 20%, -webkit-keyframes bounce 50%, -webkit-keyframes bounce 80%, -webkit-keyframes bounce 100% {
  -webkit-transform: translateY(0); }

-webkit-keyframes bounce 40% {
  -webkit-transform: translateY(-30px); }

-webkit-keyframes bounce 60% {
  -webkit-transform: translateY(-15px); }

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0); }
  40% {
    transform: translateY(-30px); }
  60% {
    transform: translateY(-15px); } }

.bounce {
  -webkit-animation-name: bounce;
  animation-name: bounce; }

-webkit-keyframes swing 20%, -webkit-keyframes swing 40%, -webkit-keyframes swing 60%, -webkit-keyframes swing 80%, -webkit-keyframes swing 100% {
